<template>
    <div>
        <FetchList ref="table" title="订单列表" :columns="columns" :getData="list" :right="24">
            <Form inline slot="head-right">
                <FormItem>
                    <Input v-model="search.value" style="width:260px;">
                        <Select v-model="search.type" slot="prepend" style="width:100px">
                            <Option :value="item.value" v-for="(item, index) of searchList" :key="index">
                                {{item.title}}
                            </Option>
                        </Select>
                    </Input>
                </FormItem>
                <FormItem>
                    <Button type="info" @click="$refs.table.fetchList()">搜索</Button>
                </FormItem>
            </Form>
        </FetchList>

        <Modal title="修改订单状态" @on-ok="changeStatus" v-model="form.is_show">
            <Select v-model="form.status">
                <Option v-for="(item, index) in form.select" :value="item.status" :key="index">
                    {{item.value}}
                </Option>
            </Select>
        </Modal>

        <Modal title="订单详情" v-model="info.is_show" width="1000">
            <div class="order-title">
                <span>当前订单状态：{{info.data.status_text}}</span>
            </div>

            <Divider orientation="left">基本信息</Divider>
            <table class="order-table" border="1">
                <tr>
                    <th>订单号</th>
                    <th>订单商品</th>
                    <th>下单人</th>
                    <th>下单时间</th>
                    <th>是否确认</th>
                </tr>
                <tr>
                    <td>{{info.data.code}}</td>
                    <td>{{info.data.title}}</td>
                    <td>{{info.data.username}}</td>
                    <td>{{info.data.time}}</td>
                    <td>{{info.data.affirm}}</td>
                </tr>
            </table>

            <Divider orientation="left">商品信息</Divider>
            <table class="order-table" border="1">
                <tr>
                    <th>商品名称</th>
                    <th>所属分类</th>
                    <th>数量</th>
                    <th>价格</th>
                    <th>商品图片</th>
                </tr>
                <tr v-for="(goods , index) in info.data.order_goods" :key="index">
                    <td>{{goods.name}}</td>
                    <td>{{goods.cate}}</td>
                    <td>{{goods.num}}</td>
                    <td>{{goods.sell_price}}</td>
                    <td><img :src="goods.img" width="150px" @click="picture.is_show = true; picture.src = goods.img"></td>
                </tr>
            </table>

            <Divider orientation="left">联系方式</Divider>
            <table class="order-table" border="1">
                <tr>
                    <th>联系人</th>
                    <th>联系电话</th>
                    <th>邮箱</th>
                    <th>联系地址</th>
                </tr>
                <tr>
                    <td>{{info.data.contact.name}}</td>
                    <td>{{info.data.contact.mobile}}</td>
                    <td>{{info.data.contact.email}}</td>
                    <td>{{info.data.contact.address}}</td>
                </tr>
            </table>
        </Modal>

        <PictureView :is_show="picture.is_show" :src="picture.src" @closePicture="picture.is_show = false"></PictureView>
    </div>
</template>

<script>
import FetchList from '@/components/table'
import PictureView from '@/components/pictureView'

export default {
    components:{
        FetchList,
        PictureView
    },

    data() {
        return {
            picture:{
                is_show:false,
                src:'',
            },
            form:{
                is_show:false,
                id : 0,
                status:0,
                select:[
                    {
                        value:"待收货",
                        status:2,
                    },
                    {
                        value:"已取消",
                        status:6,
                    }
                ],
            },
            info:{
                is_show:false,
                data:{
                    affirm: "",
                    code: "",
                    contact: {},
                    id: 0,
                    is_delete: 0,
                    is_pay: 0,
                    order_goods: [],
                    status: 0,
                    status_text: "",
                    time: "",
                    title: "",
                    user_id: 0,
                    username: "",
                }
            },
            search:{
                type:'code',
                value:'',
            },
            searchList:[
                {
                    title:'订单号',
                    value:'code',
                },
                {
                    title:'下单人',
                    value:'username',
                },
            ],
            columns:[
                {
                    title:"订单号",
                    key :"code",
                    align:"center",
                    render:(h, param) => {
                        return h("a",{
                            on:{
                                click:() => {
                                    this.info.is_show = true;
                                    this.detail(param.row.id);
                                }
                            }
                        },param.row.code);
                    },
                },
                {
                    title:"订单商品",
                    key :"title",
                    align:"center"
                },
                {
                    title:"下单人",
                    key :"username",
                    align:"center"
                },
                {
                    title:"下单时间",
                    key :"time",
                    align:"center"
                },
                {
                    title:"订单状态",
                    key :"status_text",
                    align:"center"
                },
                {
                    title:"用户确认",
                    key :"affirm",
                    align:"center",
                    width:"90px"
                },
                {
                    title:"操作",
                    align:"center",
                    render :(h,param) =>  {
                        return h("div",[
                            h("Button",{
                                props:{
                                    type:"error",
                                    size:"small",
                                    disabled:param.row.status == 6 ? false : true
                                },
                                style:{
                                    margin:"0 5px",
                                },
                                on:{
                                    click:() => {
                                        this.$Modal.confirm({
                                            title:'提示',
                                            content: "确定要删除此账户吗",
                                            onOk: () => {
                                                this.delete(param.row.id);
                                            }
                                        });
                                    }
                                }
                            },'删除'),
                            h("Button",{
                                props:{
                                    type:"warning",
                                    size:"small",
                                    disabled:param.row.status > 4 ? true : false
                                },
                                style:{
                                    margin:"0 5px",
                                },
                                on:{
                                    click:() => {
                                        this.form.is_show = true;
                                        this.form.id = param.row.id;
                                    }
                                }
                            },'修改状态'),
                        ]);
                    },
                },
            ],
        }
    },

    mounted() {
        this.$refs.table.fetchList();
    },

    methods: {
        list(param) {
            param[this.search.type] = this.search.value;

            return [this.$api.order.list, param];
        },

        async delete(id) {
            let res = await this.$api.order.delete({id});
            if (res) {
                this.$refs.table.fetchList();
                this.$Message.success('删除成功');
            }
        },

        async detail(id) {
            this.$Spin.show();
            let res = await this.$api.order.info({id});
            if (res) {
                this.info.data = res;
            }   
            this.$Spin.hide();
        },

        async changeStatus() {
            let res = await this.$api.order.changeStatus({
                id:this.form.id,
                status:this.form.status,
            });
            if (res) {
                this.$refs.table.fetchList();
                this.$Message.success('操作成功');
            }
        }
    },
}
</script>

<style lang="less" scoped>
.order-title{
    width: 100%;
    height: 50px;
    font-size: 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    padding: 0 15px;
    -ms-flex-pack: justify;
    justify-content: space-between;
    background: #f1f4f6;
    margin-bottom: 10px;
    span{
        color:red;
    }
}
.order-table{
    width:100%;
    border-collapse:collapse;
    font-size:16px; 
    text-align:center;
    border:1px solid #ccc;
    td{
        height: 50px;
        line-height: 50px;
    }
}
</style>

